/* eslint-disable max-lines */
import React from 'react';
import PropTypes from 'prop-types';
import { compact } from 'lodash';
import DSDropdownMenu from '@elliemae/ds-dropdownmenu';
import DSButton from '@elliemae/ds-basic/Button';
import { PopperPositions as Position } from '@elliemae/ds-basic/Popper';
import { Menu, MenuCombobox } from '@elliemae/ds-basic/Menu';
import SearchSmall from '@elliemae/ds-icons/SearchSmall';

function TextFilterMenu({
  column,
  menuStyle = {},
  className,
  filters = [],
  isMenuOpened = false,
  onOpenMenu = () => null,
  onClose = () => null,
  placement = Position.BOTTOM_END,
  menuProps = {},
  onAddFilter = () => null,
  onRemoveFilter = () => null,
  maxOptions = 10,
  isMulti = false,
}) {
  const { options, ...otherMenuProps } = menuProps;

  const generateOption = (option, idx) => (
    <Menu.ItemCheckbox key={idx} label={option.label} name={option.id} />
  );

  const renderOptions = (arr) => arr.map(generateOption);

  const filtersForColumn = filters.filter((f) => f.group === column.property);

  const selectedOptions = filtersForColumn.map((f) => f.value);

  const filterGroup = column.searchBy || column.property;

  return (
    <DSDropdownMenu
      closeMenuOnItemSelection
      contentStyle={menuStyle}
      customMenu={
        isMulti ? (
          <Menu containerProps={{ id: 'my-custom-id' }} visible>
            <Menu.SelectionGroup
              active={selectedOptions}
              onSelect={(newValues) => {
                let newValue = newValues.slice(-1)[0];
                if (selectedOptions.length < newValues.length) {
                  // we have to add a filter
                  onAddFilter(
                    {
                      group: filterGroup,
                      label: column.label,
                      value: newValue,
                      operator: 'contains',
                      transformRowValue: column.valueTransformation,
                    },
                    column,
                  );
                } else {
                  // We have to remove a filter
                  [newValue] = selectedOptions.filter(
                    (e) => !newValues.includes(e),
                  );
                  const nextFilters = filters.filter(
                    (filter) =>
                      !(
                        filter.group === filterGroup &&
                        filter.value === newValue
                      ),
                  );
                  onRemoveFilter({
                    group: filterGroup,
                    pill: { value: newValue },
                    nextFilters,
                  });
                }
              }}
              focusOnOpen
              multi
              searchable
            >
              {renderOptions(options)}
            </Menu.SelectionGroup>
          </Menu>
        ) : (
          <MenuCombobox
            {...otherMenuProps}
            className={className}
            dropdownFilterOptions={{
              ...column.dropdownFilterOptions,
              maxOptions,
            }}
            maxWidth={column.filterMenuMaxWidth}
            minWidth={column.filterMenuMinWidth}
            onSelectMenuItem={(selectedValue) => {
              if (selectedValue) {
                onAddFilter(
                  {
                    group: column.searchBy || column.property,
                    label: column.label,
                    value: selectedValue,
                    operator: 'contains',
                    transformRowValue: column.valueTransformation,
                  },
                  column,
                );
                onOpenMenu(false);
              }
            }}
          />
        )
      }
      isOpen={isMenuOpened}
      maxWidth={column.filterMenuMaxWidth}
      menuProps={menuProps}
      minWidth={column.filterMenuMinWidth}
      onOpenMenu={onOpenMenu}
      onClose={onClose}
      options={compact(options)}
      placement={placement}
      triggerComponent={
        <DSButton
          buttonType="text"
          icon={<SearchSmall />}
          onClick={() => (isMenuOpened ? onOpenMenu(false) : onOpenMenu(true))}
          size="s"
        />
      }
    />
  );
}

TextFilterMenu.propTypes = {
  column: PropTypes.any,
  menuStyle: PropTypes.shape({}),
  className: PropTypes.string,
  filters: PropTypes.arrayOf(PropTypes.shape({})),
  isMenuOpened: PropTypes.bool,
  onOpenMenu: PropTypes.func,
  onClose: PropTypes.func,
  placement: PropTypes.string,
  menuProps: PropTypes.shape({}),
  onAddFilter: PropTypes.func,
  onRemoveFilter: PropTypes.func,
  maxOptions: PropTypes.number,
  isMulti: PropTypes.bool,
};

export default TextFilterMenu;
