import React, { useRef, useState, useEffect } from 'react';
import DSTooltip from '@elliemae/ds-basic/Tooltip';
import useFocusGroupItem from '@elliemae/ds-shared/FocusGroup/useFocusGroupItem';
import { Pill } from './Pill';

export function OverflowPill({
  containerProps = {},
  label = '',
  options = [],
}) {
  const ref = useRef(null);
  const [tooltipIsOpen, setTooltipIsOpen] = useState(false);
  const [tooltipText, setTooltipText] = useState('');

  useEffect(() => {
    let text = '';
    options.forEach(option => {
      if (!tooltipText || option === options[0]) {
        text = `${option.label}`;
      } else {
        text = text.concat(`, ${option.label}`);
      }
    });
    setTooltipText(text);
  }, [options]);

  useFocusGroupItem(ref);

  return (
    <DSTooltip
      isOpen={tooltipIsOpen}
      title={tooltipText}
      zIndex={11}
      triggerComponent={
        <Pill
          className="overflow-pill"
          containerProps={containerProps}
          innerRef={ref}
          label={label}
          tabIndex={-1}
          onMouseEnter={() => setTooltipIsOpen(true)}
          onMouseLeave={() => setTooltipIsOpen(false)}
        />
      }
    />
  );
}
